	onHide: function() {
		console.log('App Hide');
	},
	// globalData: {
	//   Service: Service
	// },
	methods:{
		getQueryString(name) {
			var reg = new RegExp("[?&]" + name + "=([^&#]*)", "i");
			var res = window.location.href.match(reg);
		 
			if( res && res.length>1 ){
				return decodeURIComponent(res[1]);
			}
			return '';
		},
		getUrlQueryString(url, name) {
			var reg = new RegExp("[?&]" + name + "=([^&#]*)", "i");
			var res = url.match(reg);
		 
			if( res && res.length>1 ){
				return decodeURIComponent(res[1]);
			}
			return '';
		}
		
	}
};
